import { PiletApi } from "piral-core";
import * as React from "react";
import LandingPage from "../landing";
import { layout } from "../layout";

export function setupShell(app: PiletApi) {
  //   LAYOUTS REGISTRATION
  app.registerPageLayout("dashboard", ({ children }) =>
    layout({
      children: children,
    })
  );

  app.registerPageLayout("landing", ({ children }) => (
    <>
      <div>Header Landing</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPageLayout("member", ({ children }) => (
    <>
      <div>Headers Member</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPage("/", LandingPage, {
    layout: "dashboard",
  });
}
